<template>
  <div class="address_book">
    <div class="address_b_header">
      <CContentHeader content="" />
    </div>

    <div class="address_b_con flex">
      <div class="adbc_bmnav flex flex-column">
        <p class="adbc_bmnav_title"></p>
        <div class="adbc_bmnav_search flex">
          <div class="adbc_bmnav_searchwrap">
            <img class="adbc_bmnav_searchwrap_icon" :src="require('../../assets/img/addressBook/search.png')" alt />
            <el-input placeholder="" v-model="input"></el-input>
          </div>
          <el-button type="primary" @click="addDepartment"></el-button>
        </div>

        <div class="adbc_bmcon flex-one">
          <!-- <DepTree @item="clickDep" @loadMore="depLoadMore" :corpData="corpList"/> -->
          <!-- id ad10 -->
          <DepTree corpTreeId="ad10" searchType="dep" :isUseSpot="true" />
        </div>
      </div>

      <div class="adbc_list">
        <p class="adbc_bmnav_title">{{clickShowDep.name}}</p>

        <div class="adbc_list_opeara flex">
          <div class="adbc_list_opeara_list">
            <!-- <el-button type="primary"></el-button> -->
            <el-button type="primary" @click="changeDepartment"></el-button>
            <el-button type="danger" @click="deleteUesr"></el-button>
          </div>
          <div class="adbc_list_opeara_right">
            <el-button type="primary" @click="addNewMember"></el-button>
          </div>
        </div>

        <div class="abdc_list_con">
          <template>
            <el-table :data="tableData" ref="multipleTable" @selection-change="handleSelectionChange" style="width: 100%" :header-cell-style="setTableNavColor">
              <!-- <el-table-column max-width="50">
                <div class="adbc_list_con_itemselect">
                  <img src="https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=2210972854,1802121898&fm=27&gp=0.jpg" alt="">
                </div>
              </el-table-column>-->
              <el-table-column type="selection" width="40"></el-table-column>
              <el-table-column prop="name" label=""></el-table-column>
              <el-table-column prop="position" label=""></el-table-column>
              <el-table-column prop="id" label=""></el-table-column>
              <el-table-column prop="phone" label="" min-width="100"></el-table-column>
              <el-table-column prop="email" label="" min-width="150"></el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <div class="abdc_list_con_opearte flex">
                    <p>{{(scope.row.isEnable === 0)?'':'' }}</p>
                    <!-- abdc_list_con_opearte_wrap_active abdc_list_con_opearte_wrap_nactive  -->
                    <div class="abdc_list_con_opearte_wrap" :class="[(scope.row.isEnable === 0)?'abdc_list_con_opearte_wrap_nactive':'abdc_list_con_opearte_wrap_active']" @click="handleEnable(scope.row)">
                      <img class="abdc_list_con_opearte_icon" :src="require('../../assets/img/addressBook/slide.png')" />
                    </div>
                  </div>
                </template>
              </el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <span v-if="scope.row.isActive=== 0" @click="invitationActive(scope.row)" class="ab_invitation_active"></span>
                  <span v-if="scope.row.isActive=== 1"></span>
                  <!-- <span>{{getAAA(scope.row)}}</span> -->
                </template>
              </el-table-column>
            </el-table>
          </template>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="select_dep">
      <MaskLayer title="" corpTreeId="ad12" :dialogShow.sync="dialogShow" searchType="dep" twoTitle="" :powerList="powerList" @submit="submitBtn" @reset="resetBtn"></MaskLayer>
    </div>

    <!--  -->
    <div class="ab_add_department" v-if="isShowCover" @click="clickCoverBlank">
      <!--  -->
      <div class="ab_add_department_wrap" v-if="isShowAddDepartments" @click="clodeAddCoverStop">
        <div class="ab_add_department_wrap_h clearfix">
          <p class="ab_add_department_wrap_h_title fl"></p>
          <div @click="clodeAddCover" class="ab_add_department_wrap_h_close_wrap fr flex flex-item">
            <img class="ab_add_department_wrap_h_close" :src="require('../../assets/img/addressBook/close.png')" />
          </div>
        </div>

        <div class="ad_add_d_inp ad_add_d_inp1 flex flex-item">
          <p></p>
          <el-input placeholder="" v-model="departInput"></el-input>
        </div>

        <!-- ad_add_d_inp2_focus  class  -->
        <div class="ad_add_d_inp ad_add_d_inp2 flex flex-item">
          <p></p>
          <el-input placeholder="" :class="{'active': isFocusSelectDepartInput}" @focus="selectDepartInputFocus" v-model="selectDepartInput.val"></el-input>
          <img class="ad_add_d_inp2_icon" :src="require('../../assets/img/addressBook/open2.png')" />
          <div class="ad_add_d_inp2_list" v-if="isFocusSelectDepartInput">
            <DepTree corpTreeId="ad11" :searchType="'dep'" />
          </div>
        </div>
        <div class="ad_add_btn_wrap">
          <p class="ad_add_btn" @click="addDepBtn"></p>
        </div>
      </div>

      <!--  -->
      <div class="ab_add_add_newmemeter" v-if="isShowAddNewMember" @click="clodeAddCoverStop">
        <div class="ab_add_department_wrap_h clearfix">
          <p class="ab_add_department_wrap_h_title fl"></p>
          <div @click="clodeAddCover" class="ab_add_department_wrap_h_close_wrap fr flex flex-item">
            <img class="ab_add_department_wrap_h_close" :src="require('../../assets/img/addressBook/close.png')" />
          </div>
        </div>
        <div class="ad_add_newmember_con">
          <p class="ad_add_newmember_con_title"></p>
          <p class="ad_add_newmember_con_tips"></p>
        </div>
        <div class="ad_add_newmember_con_qrcode">
          <img :src="require('../../assets/img/addressBook/qrcode.png')" alt />
        </div>
        <div class="ad_add_newmember_con_copyurl"></div>
      </div>

      <!--  -->
      <div class="ad_add_child_dep">
        <div class="ab_add_department_wrap" v-if="isShowAddDep" @click="clodeAddCoverStop">
          <div class="ab_add_department_wrap_h clearfix">
            <p class="ab_add_department_wrap_h_title fl">{{isAddOrChange == 'add' ? '':''}}</p>
            <div @click="clodeAddCover" class="ab_add_department_wrap_h_close_wrap fr flex flex-item">
              <img class="ab_add_department_wrap_h_close" :src="require('../../assets/img/addressBook/close.png')" />
            </div>
          </div>

          <div class="ad_add_d_inp ad_add_d_inp1 flex flex-item">
            <p></p>
            <el-input placeholder="" autofocus="true" v-model="addChildrendepInput"></el-input>
          </div>

          <div class="ad_add_btn_wrap">
            <p class="ad_add_btn" @click="addChildrenDepBtn">{{isAddOrChange == 'add' ? '':''}}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import DepTree from "../../components/c_departmentTree.vue";
import MaskLayer from "../../components/c_maskLayer";
export default {
  data() {
    return {
      input: "",
      isShowCover: false, // 
      isShowAddDepartments: false, // 
      isShowAddNewMember: false, // 
      isShowDelMember: false, // 
      isShowAddDep: false, // 

      departInput: "", // 
      selectDepartInput: {
        // 
        val: "",
        depid: ""
      }, // 

      //  
      isAddOrChange: "", //  add  'change'
      addChildrendepParentId: "", //id
      addChildrendepInput: "", // 
      changeDepNameId: "", // id

      clickShowDep: {}, // 

      isFocusSelectDepartInput: false,

      isFocusSelect: false, // input

      multipleSelection: [], // element 
      tableData: [
        // {
        //   id: "11",
        //   name: "",
        //   position: "root",
        //   phone: "1591992104",
        //   email: "60178917@qq.com",
        //   isEnable: 0,
        //   isActive: 1
        // },
        // {
        //   id: "11",
        //   name: "",
        //   position: "root",
        //   phone: "1591992104",
        //   email: "60178917@qq.com",
        //   isEnable: 0,
        //   isActive: 1
        // }
      ],

      // 
      dialogShow: false,
      powerList: []
    };
  },

  methods: {
    //  start

    // 
    changeDepartment() {
      console.log("");
      console.log(this.multipleSelection);
      if (this.multipleSelection.length === 0) {
        return this.$zero.ToastError("");
      }
      // 
      this.dialogShow = true;
    },

    // 
    submitBtn() {
      console.log("");
      console.log(this.powerList);
      if (this.powerList.length === 0) {
        return this.$zero.ToastError("");
      }

      let useridArr = [];
      for (let i = 0; i < this.multipleSelection.length; i++) {
        useridArr.push(this.multipleSelection[i].id);
      }

      this.changeUserInDep({
        userid: useridArr.join(","),
        type: 2,
        to: this.powerList[0].depid,
        from: this.clickShowDep.depid
      });
    },

    // 
    async changeUserInDep(obj) {
      let company_id = this.$zero.getCompany_id();
      if (!company_id) {
        return this.$zero.ToastError("id");
      }
      //  from
      //   from
      let postObj = {
        company_id: company_id,
        userid: obj.userid,
        type: obj.type,
        depid: obj.depid,
        to: obj.to
      };

      if (!this.clickShowDep.isCorp) {
        postObj.from = obj.from;
      }

      let changeResult = await this.$api.addressBook.changeUserInDep(postObj);

      if (changeResult.statusCode === 72305) {
        return this.$zero.ToastError("");
      } else if (changeResult.statusCode === 72315) {
        return this.$zero.ToastError("id");
      } else if (changeResult.statusCode === 75500) {
        return this.$zero.ToastError("");
      }

      if (changeResult.statusCode !== 75200) {
        return this.$zero.ToastError(
          `:${changeResult.statusCode}`
        );
      }

      // 
      this.$zero.ToastSuccess("");
      this.dialogShow = false;
      this.powerList = [];

      // console.log("");
      // console.log("");
      // console.log(this.clickShowDep);

      if (this.clickShowDep.isCorp) {
        //  
        // 
        this.clearAllSelection();
        return;
      }

      // 
      // var deleteCount = 0;
      for (let i = this.tableData.length - 1; i >= 0; i--) {
        for (let j = 0; j < this.multipleSelection.length; j++) {
          if (this.tableData[i].id === this.multipleSelection[j].id) {
            this.tableData.splice(i, 1);
            break;
          }
        }
      }
    },

    // 
    clearAllSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },

    resetBtn() {
      console.log("");
    },
    //  end

    // / 
    async addChildrenDepBtn() {
      console.log("");
      console.log(this.addChildrendepInput);
      // 
      let childrenValue = this.addChildrendepInput.trim();
      // if(this.addChildrenDepBtn)
      if (!childrenValue) {
        return this.$zero.ToastError("");
      }
      // 
      if (this.isAddOrChange === "add") {
        //  
        this.postAddDep({
          name: childrenValue,
          parentId: this.addChildrendepParentId
        });
      } else if (this.isAddOrChange === "change") {
        // 
        console.log("");
        this.updateDepName({
          name: this.addChildrendepInput,
          parentId: this.addChildrendepParentId,
          depid: this.changeDepNameId
        });
      } else {
        return this.$zero.ToastError(",");
      }
    },

    /**
     * 
     * @params {Object} obj
     *  obj.name {String} 
     *  obj.parentId {String} id
     *  obj.depid {String} id
     */
    async updateDepName(obj) {
      let company_id = this.$zero.getCompany_id();
      if (!company_id) {
        return this.$zero.ToastError("id");
      }
      let updateResult = await this.$api.addressBook.updateDepName({
        company_id: company_id,
        depid: obj.depid,
        name: obj.name,
        parentId: obj.parentId
      });

      console.log("");
      console.log(updateResult);
      if (updateResult.statusCode === 72305) {
        return this.$zero.ToastError("");
      } else if (updateResult.statusCode === 72315) {
        return this.$zero.ToastError("id");
      } else if (updateResult.statusCode === 75500) {
        return this.$zero.ToastError("");
      }

      if (updateResult.statusCode !== 75200) {
        return this.$zero.ToastError(
          `${updateResult.statusCode}`
        );
      }

      // 
      // console.log('');
      this.$zero.ToastSuccess("");
      this.clearCoverData();
      //  
      this.$zero.eventBus.$emit("ad10changeDep", {
        depid: obj.depid,
        name: obj.name
      });
    },

    // 
    async submitDelMember() {
      console.log("");
      let delUser = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        delUser.push(this.multipleSelection[i].id);
      }
      // console.log('');
      // console.log(delUser);
      let delUserStr = delUser.join(",");
      // console.log(delUserStr);

      let result = await this.$api.addressBook.deleteUserByUserid({
        company_id: "5d1463a61609d536e0bf2a1e",
        userid: delUserStr
      });
      console.log("");
      console.log(result);
      this.clearCoverData();

      if (result.statusCode === 72305) {
        return this.$zero.ToastError("");
      } else if (result.statusCode === 72315) {
        return this.$zero.ToastError("id");
      } else if (result.statusCode === 75500) {
        return this.$zero.ToastError("");
      }

      if (result.statusCode !== 75200) {
        return this.$zero.ToastError(`${result.statusCode}`);
      }

      // 
      this.$zero.ToastSuccess("");
      //  tableData
      // var deleteCount = 0;
      for (let i = this.tableData.length - 1; i >= 0; i--) {
        for (let j = 0; j < this.multipleSelection.length; j++) {
          if (this.tableData[i].id === this.multipleSelection[j].id) {
            this.tableData.splice(i, 1);
            break;
          }
        }
      }
      this.multipleSelection = [];
    },

    // 
    async deleteUesr() {
      let deleteNum = this.multipleSelection.length;
      if (deleteNum === 0) {
        return this.$zero.ToastError("");
      }
      // this.isShowCover = true;
      // this.isShowDelMember = true;

      let isSubmit = await this.$zero.DeleteTips("?");

      if (!isSubmit) {
        return;
      }
      // 
      this.submitDelMember();
    },

    // 
    async getComponetTopDepid() {
      let result = await this.$api.addressBook.getDepAllMember({});
      // console.log(result);
      var all = [];
      for (let i = 0; i < result.list.length; i++) {
        var userInfoItem = result.list[i];
        // console.log(userInfoItem);
        var obj = {
          id: userInfoItem.userid,
          name: userInfoItem.name,
          position: userInfoItem.openid.position,
          phone: userInfoItem.openid.phone,
          email: userInfoItem.openid.email,
          isEnable: userInfoItem.enable,
          isActive: userInfoItem.activation
        };
        all.push(obj);
      }

      // this.tableData = this.tableData.concat(all);
      this.tableData = all;
      this.initScroll();
    },

    // 
    handleEnable(item) {
      console.log("");
      console.log(item);
      var changeType = item.isEnable;
      //    0 --> 1    1--->0
      // 0   1 -->   1 2
      changeType = changeType === 0 ? 2 : 1;

      var obj = {
        userid: item.id,
        type: changeType
      };
      this.toggleEnableStatus(obj, item);
    },

    // 
    invitationActive(item) {
      console.log("");
      console.log(item);
    },

    // 
    setTableNavColor() {
      return "background:#f2f2f2";
    },

    //  X 
    clodeAddCover() {
      this.clearCoverData();
    },
    // 
    clodeAddCoverStop(e) {
      e.stopPropagation();
      e.cancelBubble = true;
    },

    // 
    clickCoverBlank() {
      this.clearCoverData();
    },

    // 
    selectDepartInputFocus() {
      console.log("");
      this.isFocusSelectDepartInput = true;
    },

    // 
    addDepartment() {
      console.log("");
      this.isShowCover = true;
      this.isShowAddDepartments = true;
    },

    // 
    addDepBtn() {
      console.log(this.departInput);
      if (!this.departInput.trim()) {
        return this.$zero.ToastError("");
      }
      if (!this.selectDepartInput.depid) {
        return this.$zero.ToastError("");
      }
      // API
      this.postAddDep({
        name: this.departInput.trim(),
        parentId: this.selectDepartInput.depid
      });
    },

    /**
     * 
     * @params obj {Object}
     *  obj.parentId {String} id
     *  obj.name {String} 
     */
    async postAddDep(obj) {
      // obj.company_id {String} id
      let company_id = this.$zero.getCompany_id();
      if (!company_id) {
        return this.$zero.ToastError("id");
      }
      let result = await this.$api.addressBook.addDepartment({
        name: obj.name,
        parentId: obj.parentId,
        company_id: company_id
      });

      console.log("");
      console.log(result);

      if (result.statusCode === 72305) {
        return this.$zero.ToastError("");
      } else if (result.statusCode === 72315) {
        return this.$zero.ToastError("id");
      } else if (result.statusCode === 75500) {
        return this.$zero.ToastError("");
      }

      if (result.statusCode !== 75200) {
        return this.$zero.ToastError(`${result.statusCode}`);
      }

      // 
      this.$zero.ToastSuccess("");
      //  
      this.clearCoverData();
      let newDepInfo = {
        depid: result.depid,
        name: obj.name,
        parentId: obj.parentId,
        type: "dep",
        show: false // 
      };
      console.log("");
      console.log(newDepInfo);

      // 
      this.$zero.eventBus.$emit("ad10Add", {
        depinfo: newDepInfo
      });
    },

    /**
     * 
     * @params obj {Object}
     *  obj.company_id {String} id
     *  obj.userid {String} id
     *  obj.type {Number} 
     * @params item {Object} 
     */
    async toggleEnableStatus(obj, item) {
      // console.log(arguments);

      let company_id = this.$zero.getCompany_id();
      if (!company_id) {
        return this.$zero.ToastError('id');
      }

      let result = await this.$api.addressBook.toggleEnableStatus({
        company_id: company_id,
        userid: obj.userid,
        type: obj.type
      });

      console.log("");
      console.log(result);

      if (result.statusCode === 75500) {
        return this.$zero.ToastError("");
      }

      if (result.statusCode !== 75200) {
        return this.$zero.ToastError(`${result.statusCode}`);
      }

      // 
      this.$zero.ToastSuccess("");
      if (obj.type === 1) {
        item.isEnable = 0;
      } else if (obj.type === 2) {
        item.isEnable = 1;
      }
    },

    /**
     * 
     * @params obj {Object}
     */
    async deleteUserByUserid(obj) {
      let result = await this.$api.addressBook.deleteUserByUserid({
        company_id: obj.company_id,
        userid: obj.userid
      });
      console.log("");
      console.log(result);

      if (result.statusCode === 72305) {
        return this.$zero.ToastError("");
      } else if (result.statusCode === 72315) {
        return this.$zero.ToastError("id");
      } else if (result.statusCode === 75500) {
        return this.$zero.ToastError("");
      }

      if (result.statusCode !== 75200) {
        return this.$zero.ToastError(`${result.statusCode}`);
      }

      // 
      this.$zero.ToastSuccess("");
    },

    // 
    addNewMember() {
      console.log("");
      this.isShowCover = true;
      this.isShowAddNewMember = true;
    },

    // 
    clearCoverData() {
      this.isShowCover = false;
      this.isShowAddDepartments = false;
      this.isShowAddNewMember = false;
      this.isFocusSelectDepartInput = false;
      this.isShowDelMember = false;
      this.isShowAddDep = false;
      this.departInput = "";
      this.selectDepartInput = {
        val: "",
        depid: ""
      };
      this.isAddOrChange = "";
      this.addChildrendepParentId = "";
      this.changeDepNameId = "";
      this.addChildrendepInput = "";
    },

    // 
    handleSelectionChange(val) {
      console.log("");
      console.log(val);
      this.multipleSelection = val;
    },

    /**
     * id
     * @params {Object} obj
     *  obj.userid {String} id
     */
    async getUserDetail(obj) {
      let company_id = this.$zero.getCompany_id();
      if (!company_id) {
        return this.$zero.ToastError("company_id,");
      }

      let userDetail = await this.$api.addressBook.getUserDetail({
        company_id: company_id,
        userid: obj.userid
      });

      if (!userDetail.statusCode === 72305) {
        return this.$zero.ToastError(",");
      } else if (userDetail.statusCode === 72315) {
        return this.$zero.ToastError("id");
      } else if (userDetail.statusCode === 75500) {
        return this.$zero.ToastError("");
      }

      if (userDetail.statusCode !== 75200) {
        return this.$zero.ToastError(
          `:${userDetail.statusCode}`
        );
      }

      // 
      console.log("");
      console.log(userDetail);
      console.log(userDetail.info);
      var userItem = {
        id: userDetail.info.userid,
        name: userDetail.info.name,
        position: userDetail.info.position,
        phone: userDetail.info.phone,
        email: userDetail.info.email,
        isEnable: userDetail.info.enable,
        isActive: userDetail.info.activation
      };
      this.tableData = [userItem];
    },

    //  id 
    async getMemberByDepid(depid) {
      let result = await this.$api.addressBook.getDepAllMember({
        depid: depid
      });
      console.log("");
      console.log(result);
      var all = [];
      for (let i = 0; i < result.list.length; i++) {
        var userInfoItem = result.list[i];
        // console.log(userInfoItem);
        var obj = {
          id: userInfoItem.userid,
          name: userInfoItem.name,
          position: userInfoItem.openid.position,
          phone: userInfoItem.openid.phone,
          email: userInfoItem.openid.email,
          isEnable: userInfoItem.enable,
          isActive: userInfoItem.activation
        };
        all.push(obj);
      }

      // this.tableData = this.tableData.concat(all);
      this.tableData = all;
      this.initScroll();
    },

    // 
    clickParentDepTxt(item) {
      this.selectDepartInput.val = item.name;
      this.selectDepartInput.depid = item.depid;
      // 
      this.isFocusSelectDepartInput = false;
    },

    // 
    async deleteDepFn({ depid, name }) {
      let company_id = this.$zero.getCompany_id();
      if (!company_id) {
        return this.$zero.ToastError("id");
      }

      // 
      let isSubmit = await this.$zero.DeleteTips(`${name}`);
      if (!isSubmit) {
        return;
      }

      // 
      let deleteResult = await this.$api.addressBook.deleteDep({
        company_id: company_id,
        depid: depid
      });

      if (deleteResult.statusCode === 72309) {
        return this.$zero.ToastError(
          ""
        );
      } else if (deleteResult.statusCode === 72315) {
        return this.$zero.ToastError("id");
      } else if (deleteResult.statusCode === 75500) {
        return this.$zero.ToastError("");
      }

      if (deleteResult.statusCode !== 75200) {
        return this.$zero.ToastError(
          `:${deleteResult.statusCode}`
        );
      }

      // 
      this.$zero.ToastSuccess("");
      // 
      this.$zero.eventBus.$emit("ad10Del", { depid }); // id 
    },

    //  
    clickDepEmit(obj) {
      let { item, corpTreeId } = obj;
      console.log("id ==== " + corpTreeId + "");

      // corpTreeId   ad10  ad11 ad12
      if (corpTreeId === "ad10") {
        // 
        // console.log(corpTreeId);
        // console.log('');
        console.log("");
        console.log(item);

        this.clickShowDep = item; // 
        //  
        if (item.isCorp) {
          // 
          this.getComponetTopDepid();
        } else if (item.type === "dep") {
          // 
          this.getMemberByDepid(item.depid);
        } else if (item.type === "user") {
          //  
          // 
          this.getUserDetail({ userid: item.depid });
        }
      } else if (corpTreeId === "ad11") {
        //  
        console.log(" ");
        // console.log(corpTreeId);
        // console.log('');
        this.clickParentDepTxt(item);
      } else if (corpTreeId === "ad12") {
        // 
        console.log("");
        // console.log(corpTreeId);
        // console.log('');
      }
    },

    // 
    addChildDep(obj) {
      // console.log('');
      // console.log(obj);
      let { corpTreeId, item } = obj;
      if (corpTreeId !== "ad10") {
        return;
      }

      // 
      this.isShowCover = true;
      this.isShowAddDep = true;
      this.isAddOrChange = "add"; // 
      this.addChildrendepParentId = item.depid; // idid
    },

    // 
    changeDepName(obj) {
      console.log("");
      console.log(obj);

      let { corpTreeId, item } = obj;

      if (corpTreeId !== "ad10") {
        return;
      }
      // 
      this.isShowCover = true;
      this.isShowAddDep = true;
      this.isAddOrChange = "change"; // 
      this.addChildrendepParentId = item.parent_id; // id
      this.addChildrendepInput = item.name;
      this.changeDepNameId = item.depid; // id
    },

    // 
    deleteDep(obj) {
      console.log("");
      console.log(obj);
      let { corpTreeId, item } = obj;
      if (corpTreeId !== "ad10") {
        return;
      }

      console.log("");
      // 
      this.deleteDepFn({
        depid: item.depid,
        name: item.name
      });
    },

    // 
    initScroll() {
      // 
      this.$nextTick(() => {
        $(".el-table__body-wrapper")
          .getNiceScroll()
          .remove();
        $(".el-table__body-wrapper").niceScroll(".el-table__body", {
          cursorcolor: "#2f78ef",
          cursorwidth: "5px",
          autohidemode: false,
          background: "#e6e6e6",
          preventmultitouchscrolling: true,
          bouncescroll: false
        });
      });
    },

    tellCorpDep(corpInfo) {
      // console.log("");
      // console.log(corpInfo);
      this.clickShowDep = corpInfo;
    }
  },
  //
  beforeDestroy() {
    this.$zero.eventBus.$off("addBookLoadMore");
  },
  mounted() {
    // 
    this.getComponetTopDepid();

    //  --- 
    // 
    this.$zero.eventBus.$on("departmentItem", this.clickDepEmit);

    // 
    this.$zero.eventBus.$on("addChildDep", this.addChildDep);

    // 
    this.$zero.eventBus.$on("changeDepName", this.changeDepName);

    // 
    this.$zero.eventBus.$on("deleteDep", this.deleteDep);

    // 
    this.$zero.eventBus.$on("tellCorpDep", this.tellCorpDep);

    // 
    this.initScroll();
  },

  destroyed() {
    this.$zero.eventBus.$off("departmentItem", this.clickDepEmit);
    // 
    this.$zero.eventBus.$off("addChildDep", this.addChildDep);
    // 
    this.$zero.eventBus.$off("changeDepName", this.changeDepName);
    // 
    this.$zero.eventBus.$off("deleteDep", this.deleteDep);
    // 
    this.$zero.eventBus.$off("tellCorpDep", this.tellCorpDep);
  },
  components: {
    DepTree,
    MaskLayer
  }
};
</script>

